@import url(https://fonts.googleapis.com/css2?family=Public+Sans:wght@400;500;600;700&display=swap);
@tailwind base;
@tailwind components;
@tailwind utilities;
body, html {
  font-family: 'Public Sans', sans-serif; 
 
}
body {
   background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1)100%);
   height: 100vh;
   background-size: 150px;
}
 body .dark-mode {
  background-color: black;
  margin-bottom: 0px;
  position: absolute;
  height: 100%;
  width: 100%;
}
h1 {
  font-size: 1.5rem !important;
}
h3 {
  font-size: 1.1rem !important;
}
h2 {
  font-size: 1.3rem !important;
}
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.dark-mode {
  background-color: black;
}

.dark-mode h1 {
  color: white;
}

.my-container {
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 10px;
  padding-right: 15px;
  padding-left: 15px;
  padding-bottom: 3%;
  background-color: white;
  border-radius: 15px;
}
@media screen and (max-width: 1099px) {
  .my-container {
    margin-left: 63px;
  }
}
.dark-mode .my-container {
  background: black;
  margin-bottom: 0px;
}
.notes-list {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}
.note.new {
  background-color: blue;
}
.notes-h1 {
  padding-top: 3%;
  text-align: center;
}
textarea {
  border: none;
  resize: none;
  background-color: blue;
}
textarea::-webkit-input-placeholder {
  color: white !important;
}
textarea:-ms-input-placeholder {
  color: white !important;
}
textarea::placeholder {
  color: white !important;
}
textarea:focus {
  outline: none;
}

.save {
  background-color: orange;
  border: none;
  border-radius: 15px;
  padding: 5px 10px 5px 10px;
  margin-top: 10px;
  margin-left: 135px;
}

.save:hover {
  background-color: rgb(241, 190, 95);
  cursor: pointer;
}

.note{
  background-color: #fef68a;
  border-radius: 10px;
  padding: 1rem;
  min-height: 170px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  white-space: pre-wrap;
}

.note-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.character-limit {
  color: white;
}
.delete-icon {
  cursor: pointer;
}

.search {
  display: flex;
  align-items: center;
  background-color: rgb(233, 233, 233);
  border-radius: 10px;
  padding: 5px;
  margin-bottom: 1.5em;
}
.search input {
  border: none;
  background-color: rgb(233, 233, 233);
}

.search input:focus {
  outline: none;
}

/* To Do App */
.todo-container {
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 10px;
  padding-right: 15px;
  padding-left: 15px;
  padding-bottom: 3%;
  max-height: 100vh;
  background-color: white;
  border-radius: 15px;
}
@media screen and (max-width: 1000px) {
  .todo-container {
    margin-left: 63px;
  }
}
.dark-mode .todo-container {
  background: black;
}
input {
  margin: 2px 5px;
}
#to-do-form input {
  background-color: blue;
  border: 0;
  width: 175px;
  height: 50px;
  padding: 0 20px;
  margin: 20px auto;
  border-radius: 25px;
  /*filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25)); */
  font-size: 1.1em;
  color: white;
}
@media screen and (min-width: 768px) {
  #to-do-form input {
    width: 200px;
  }
  }
  form#to-do-form {
    margin: 20px auto;
    width: 20rem;
  }
@media screen and (min-width: 768px) {
form#to-do-form {
  margin: 20px auto;
  width: 25rem;
}
}
#to-do-form input::-webkit-input-placeholder {
  color: white !important;
}
#to-do-form input:-ms-input-placeholder {
  color: white !important;
}
#to-do-form input::placeholder {
  color: white !important;
}
/* Removes outline when clicked */
#to-do-form input:focus, #to-do-form button:focus {
  outline: none;
}

#to-do-form button {
  height: 50px;
  width: 80px;
  border-radius: 25px;
  border: 0;
  background-color: orange;
  cursor: pointer;
  color: white;
  /*filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));*/
  font-size: 1.1em;
  margin-left: 25px;
}
@media screen and (min-width: 768px) {
  #to-do-form button {
    width: 100px;
  }
  }
.list {
  color: black;
  width: 19rem;
  height: 50px;
  margin: 20px auto;
  background-color: #fef68a;
  border-radius: 25px;
  /*filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));*/
}
@media screen and (min-width: 768px) {
  .list {
    width: 30rem;
  }
}
.list p {
  padding: 10px;
  position: relative;
  cursor: pointer;
  align-items: center;
}

.list p span {
  position: absolute;
  right: 10px;
  cursor: pointer;
}

.todo-delete {
  margin: 7px 5px;
  font-size: 15px;
  align-items: center;
}

.list p input {
  background-color: transparent;
  border: 0;
  font-size: 1.1em;
  outline: none;
}
@media screen and (min-width: 768px) {
  .list p input {
    font-size: 1.2em;
  }
}
.todo-h1 {
  padding-top: 3%;
  text-align: center;
}
.list__item-checked {
  opacity: 0.6;
}
/* Home Page */
.h1-home {
  text-align: center;
}

/* Weather */

.weather-background {
  background: white;
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
  border-radius: 15px;
  padding-top: 1%;
  padding-bottom: 3%;
  max-width: 960px;
  max-height: 800px;
  margin-top: 10px;
}
@media screen and (max-width: 800px) {
  .weather-background {
    margin-left: 63px;
  }
}
.dark-mode .weather-background {
  background-color: black;
}
.weather-container p {
  font-size: 1.3rem !important;
}
.weather-h1 {
  text-align: center;
}
.weather-h2 {
  font-size: 6rem !important;
  color: blue;
}
.weather-container {
  max-width: 600px;
  max-height: 700px;
  margin: auto;
  position: relative;
  background-color: #fef68a;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.top {
  width: 100%;
  margin: 1rem auto;
  text-align: center;
}
.bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  border-radius: 15px;
  background-color: orange;
}
.description {
  position: relative;
  right: -40%;
  -webkit-transform: rotate(269deg);
          transform: rotate(269deg);
  padding-left: 15%;
}
.bold {
  font-weight: 700;
}
.weather-search {
  text-align: center;
  padding: 1rem;
  background-color: blue;
  border-radius: 15px;
}
.weather-container input {
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: none;
  background-color: orange;
  text-align: center;
}
::-webkit-input-placeholder {
  color: black !important;
}
:-ms-input-placeholder {
  color: black !important;
}
::placeholder {
  color: black !important;
}
/* Budget App */

.budget-h1 {
  text-align: center;
  padding-top: 2%;
}
.grid {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}
.budget-section {
  background-color: blue;
  border-radius: 15px;
  text-align: center;
}
.remaining {
  background-color: orange;
  border-radius: 15px;
  text-align: center;
}
.expensetotal {
  background-color: #fef68a;
  border-radius: 15px;
  text-align: center;
}
.expenses {
  text-align: center;
  padding-top: 20px;
}
.expenselist {
  list-style-type: none;
}
ul {
  list-style-type: none;
}
.add-expense {
  text-align: center;
  padding-top: 20px;
}

.edit-button {
  border-radius: 25px;
  background-color: lightgrey !important;
  width: 50px;
  float: right;
  margin-top: -28px;
  margin-right: 5px;
}
.submit-buttonn {
  border-radius: 25px;
  background-color: lightgrey !important;
  width: 60px !important;
  margin-left: 5px;
} 
.submit-button {
  display: inline !important;
  width: 80px !important;
}
ul {
  list-style-type: none; /* Remove bullets */
  padding: 0; /* Remove padding */
  margin: 0; /* Remove margins */
}

.expense-list li  {
  border: 1px solid #ddd; 
  margin-top: -1px; 
  background-color: #f6f6f6; 
  padding: 20px; 
  border-radius: 15px;

}
.props-name {
  float: left;
  margin-top: -7px;
  line-height: 15px;
}
.props-cost {
  float: right;
  font-size: 1rem;
  line-height: 15px;
}
.budget-delete {
  float: right;
  padding-left: 20px;
}

.form {
  margin: 10px 20px;
}
input#cost {
  border-radius: 25px;
  width: 200px;
  height: 30px;
  background-color: lightgoldenrodyellow;
  outline: none;
  padding-left: 5px;
}
input#name {
  border-radius: 25px;
  width: 200px;
  height: 30px;
  background-color: lightgoldenrodyellow;
  outline: none;
  padding-left: 5px;
}
.dark-mode .label-name {
  color: white;
}
.label-name {
  font-size: 1rem;
}
.dark-mode .label-cost {
  color: white;
}
.label-cost {
  font-size: 1rem;
}
.budget-h2-budget {
  color: white;
}
/* Home Page */

.home-container {
  margin-left: 60px;
}
.dark-mode .my-sidebar {
  background-color: black;
}
.dark-mode .sidebar-icon {
  color: white;
}
